.btn {
  border: 2px solid black;
  padding: .7rem;
  cursor: pointer;
  background-color: rgb(204, 204, 204);
  border-radius: 4px;
}

.short-btn {
  display: inline-block;
  border: 2px solid black;
  padding: .7rem;
  cursor: pointer;
  background-color: rgb(204, 204, 204);
  border-radius: 4px;
}

form {
  outline: 1px solid black;
  padding: .5rem;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

input[type="button"],
input[type="submit"] {
  padding: 2px 6px;
  width: 4rem;
}

input[type="text"] {
  font-size: 1rem;
  outline: 1px solid black;
  border: none;
  border-radius: 4px;
  height: 1.5rem;
  padding: 0 6px;
  max-width: 10rem;

  &:focus {
    outline: 2px solid blue;
  }
}`, "",{"version":3,"sources":["webpack://./src/default.css"],"names":[],"mappings":"AAAA,+BAA+B;AAC/B;EACE,uBAAuB;EACvB,cAAc;EACd,eAAe;EACf,oCAAoC;EACpC,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,uBAAuB;EACvB,cAAc;EACd,eAAe;EACf,oCAAoC;EACpC,kBAAkB;AACpB;;AAEA;EACE,wBAAwB;EACxB,cAAc;EACd,aAAa;EACb,sBAAsB;EACtB,QAAQ;AACV;;AAEA;;EAEE,gBAAgB;EAChB,WAAW;AACb;;AAEA;EACE,eAAe;EACf,wBAAwB;EACxB,YAAY;EACZ,kBAAkB;EAClB,cAAc;EACd,cAAc;EACd,gBAAgB;;EAEhB;IACE,uBAAuB;EACzB;AACF","sourcesContent":["/* Default div button styling */\n.btn {\n  border: 2px solid black;\n  padding: .7rem;\n  cursor: pointer;\n  background-color: rgb(204, 204, 204);\n  border-radius: 4px;\n}\n\n.short-btn {\n  display: inline-block;\n  border: 2px solid black;\n  padding: .7rem;\n  cursor: pointer;\n  background-color: rgb(204, 204, 204);\n  border-radius: 4px;\n}\n\nform {\n  outline: 1px solid black;\n  padding: .5rem;\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\ninput[type=\"button\"],\ninput[type=\"submit\"] {\n  padding: 2px 6px;\n  width: 4rem;\n}\n\ninput[type=\"text\"] {\n  font-size: 1rem;\n  outline: 1px solid black;\n  border: none;\n  border-radius: 4px;\n  height: 1.5rem;\n  padding: 0 6px;\n  max-width: 10rem;\n\n  &:focus {\n    outline: 2px solid blue;\n  }\n}"],"sourceRoot":""}]);
