  --default-priority-color: linear-gradient(lightgray, gray);
  --high-priority-color: linear-gradient(red, maroon);
  --low-priority-color: linear-gradient(yellow, rgb(199, 199, 55));
  --error-text-color: red;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100svh;
  font-family: sans-serif;
}

header {
  grid-column: 1/3;
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 1rem;
}

.main-container {
  background-color: lightblue;
  height: 100svh;
  display: grid;
  grid-template-columns: minmax(max-content, 250px) 2fr;
  grid-template-rows: max-content;
}

.sidebar {
  grid-column: 1/2;
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  border: 1px solid gray;
  padding: 0 1rem;
  gap: .6rem;
  overflow-y: auto;
}

.sidebar .btn-category,
.sidebar .project-list {
  padding: 1rem;
  border: 1px solid black;
}

.sidebar .form-btn-field {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2px;
}

.main {
  border: 1px solid blue;
  overflow-y: auto;
}

.main .addTaskOverlay {
  display: flex;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}

.addTaskOverlay .addTaskContainer {
  background-color: rgb(115, 112, 112);
  border-radius: 12px;
  width: 50%;
  height: calc(max-content);
  padding: 1rem;
  box-shadow: 1px 2px 10px black;
  overflow-y: auto;
}

.addTaskContainer .closeHeader {
  display: flex;
  justify-content: end;
}

.addTaskContainer .closeHeader .short-btn {
  background-color: rgb(187, 36, 36);
  color: white;
  padding: .4em 1em;
}

.addTaskContainer .closeHeader .formSection {
  padding: .5rem 1rem;
}


.formSection .form-field,
.btn-field {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.formSection .btn-field {
  margin-top: 2rem;
}

.formSection .btn-field #addTaskFormBtn {
  border-radius: 6px;
  width: max-content;
  padding: 0.5em 1em;
  cursor: pointer;
}

.formSection select,
.formSection input[type="date"] {
  width: calc(max-content);
  height: 1.5rem;
  text-align: center;
  border-radius: 6px;
}

.formSection textarea {
  width: 230px;
  height: 55px;
  max-height: 120px;
  max-width: 350px;
  min-height: 2rem;
  min-width: 90px;
  text-align: left;
  padding: .35rem;
  border-radius: 6px;
}

.main .btn {
  border: 2px solid black;
  padding: .25em;
  cursor: pointer;
  background-color: rgb(204, 204, 204);
  border-radius: 4px;
}

.content {
  border: 1px solid red;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: max-content;
  gap: 1rem;
  overflow-y: auto;
  padding: 1rem 2rem;
}

.content .taskContent {
  display: inline-flex;
}

.taskContent .btn {
  padding: .5em 1em;
}

.color-tile {
  border-radius: 10px;
  background: var(--default-priority-color);
  box-shadow: 1px 2px 4px black;
}

.main-tile {
  background-color: rgb(228, 242, 255);
  margin: 4px 6px;
  z-index: 100;
  display: flex;
  align-items: center;
  padding: 1rem;
  border-radius: 10px;
  justify-content: space-between;
}

.main-tile .right-side {
  display: flex;
  gap: .6rem;
  align-items: center;
}

.priority-high {
  background: var(--high-priority-color);
}

.priority-low {
  background: var(--low-priority-color);
}

.btn:hover {
  outline: 1px solid blue;
}

.hide {
  display: none !important;
}


.error-msg {
  color: var(--error-text-color);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,0DAA0D;EAC1D,mDAAmD;EACnD,gEAAgE;EAChE,uBAAuB;AACzB;;AAIA;;;EAGE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,uBAAuB;AACzB;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,SAAS;EACT,mBAAmB;EACnB,aAAa;AACf;;AAEA;EACE,2BAA2B;EAC3B,cAAc;EACd,aAAa;EACb,qDAAqD;EACrD,+BAA+B;AACjC;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,eAAe;EACf,UAAU;EACV,gBAAgB;AAClB;;AAEA;;EAEE,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,oCAAoC;EACpC,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,QAAQ;EACR,SAAS;EACT,uBAAuB;EACvB,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,oCAAoC;EACpC,mBAAmB;EACnB,UAAU;EACV,yBAAyB;EACzB,aAAa;EACb,8BAA8B;EAC9B,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,kCAAkC;EAClC,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;AACrB;;;AAGA;;EAEE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,kBAAkB;EAClB,eAAe;AACjB;;AAEA;;EAEE,wBAAwB;EACxB,cAAc;EACd,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,YAAY;EACZ,iBAAiB;EACjB,gBAAgB;EAChB,gBAAgB;EAChB,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,uBAAuB;EACvB,cAAc;EACd,eAAe;EACf,oCAAoC;EACpC,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,aAAa;EACb,0BAA0B;EAC1B,2BAA2B;EAC3B,SAAS;EACT,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;EACnB,yCAAyC;EACzC,6BAA6B;AAC/B;;AAEA;EACE,oCAAoC;EACpC,eAAe;EACf,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,aAAa;EACb,mBAAmB;EACnB,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,UAAU;EACV,mBAAmB;AACrB;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,qCAAqC;AACvC;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,wBAAwB;AAC1B;;;AAGA;EACE,8BAA8B;AAChC","sourcesContent":[":root {\n  --default-priority-color: linear-gradient(lightgray, gray);\n  --high-priority-color: linear-gradient(red, maroon);\n  --low-priority-color: linear-gradient(yellow, rgb(199, 199, 55)